html,
body {
  min-height: 100%;
}

body {
  font-family: 'Fira Sans', sans-serif;
  font-size: 18px;
  text-align: center;
  
  background-repeat: no-repeat;
  background: #a740ff;
  background: -webkit-linear-gradient(top, #a740ff 0%,#a740ff 30%, #577bf6 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#a740ff),color-stop(30%, #a740ff), to(#577bf6));
  background: -o-linear-gradient(top, #a740ff 0%,#a740ff 30%, #577bf6 100%);
  background: linear-gradient(to bottom, #a740ff 0%,#a740ff 30%, #577bf6 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a740ff', endColorstr='#577bf6',GradientType=0 );
 
}

.header {
  padding: 30px 20px 7px;
  text-align: center;
  background: #577bf6;
  background: -webkit-linear-gradient(top, #577bf6 0%,#577bf6 60%, #a740ff 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#577bf6),color-stop(60%, #577bf6), to(#a740ff));
  background: -o-linear-gradient(top, #577bf6 0%,#577bf6 60%, #a740ff 100%);
  background: linear-gradient(to bottom, #577bf6 0%,#577bf6 60%, #a740ff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#577bf6', endColorstr='#a740ff',GradientType=0 );
}

.header a {
  margin-bottom: 30px;
  display: inline-block;
  color: #000;
  font-size: 22px;
  padding: 20px;
  background: #fff;
  width: 20%;
}

.app {
  margin: 30px 0;
  -webkit-box-shadow: 5px 5px 5px rgba(0,0,40px,10px,0.1);
          box-shadow: 5px 5px 5px rgba(0,0,40px,10px,0.1);
}

a {
  color: #a740ff;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

a:hover,
a:focus,
a:active {
  color: #577bf6;
  text-decoration: none;
}

.songs a {
  color: #333;
}

.songs a:hover,
.songs a:focus,
.songs a:active {
  color: #a740ff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Fira Sans', sans-serif;
  font-weight: 900;
}

h1 {
  padding: 40px 0;
  font-size: 48px;
  color: #fff;
}

img {
  max-width: 100%;
}

.track .btn {
  padding: 10px 20px;
  display: inline-block;
  margin: 10px 10px 10px 0;
  white-space: normal;
  font-size: 22px;
  font-weight: 600;
  color: #fff;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  background: #577bf6;
  background: -webkit-linear-gradient(left, #577bf6 0%, #a740ff 100%);
  background: -webkit-gradient(linear, left top, right top, from(#577bf6), to(#a740ff));
  background: -o-linear-gradient(left, #577bf6 0%, #a740ff 100%);
  background: linear-gradient(to right, #577bf6 0%, #a740ff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#577bf6', endColorstr='#a740ff',GradientType=0 );
}

.track .btn:hover,
.track .btn:focus,
.track .btn:active {
  background: #a740ff;
  background: -webkit-linear-gradient(left, #a740ff 0%, #577bf6 100%);
  background: -webkit-gradient(linear, left top, right top, from(#a740ff), to(#577bf6));
  background: -o-linear-gradient(left, #a740ff 0%, #577bf6 100%);
  background: linear-gradient(to right, #a740ff 0%, #577bf6 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a740ff', endColorstr='#577bf6',GradientType=0 );
}

select {
  width: 100%;
  padding: 15px;
  margin-bottom: 15px;
  font-size: 22px;
  -webkit-appearance: none;
  -moz-appearance:    none;
  appearance:         none;
  background-color: #fff;
  outline: none;
  cursor: pointer;
}

@media (min-width: 768px) {
  select {
    width: 70%;
  }
}

audio {
  width: 100%;
}

.songs {
  text-align: left;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  line-height: 1.7;
}

.song:nth-child(even) {
  background: #91a3fb;
}

.song {
  padding: 5px 10px;
  background: #afbdfe;
  display: -ms-flexbox;
  display: flex;
}

.song-number {
  -ms-flex: 0 0 100px;
      flex: 0 0 100px;
}

.song-title {
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  min-width: 0;
}

.song-view {
  -ms-flex: 0 0 150px;
      flex: 0 0 150px;
}

.song-play {
  -ms-flex: 0 0 100px;
      flex: 0 0 100px;
  font-size: 2em;
}

.song-number,
.song-title,
.song-view,
.song-play {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.song-number,
.song-view,
.song-play {
  -ms-flex-pack: center;
      justify-content: center;
}

@media (max-width: 700px) {
  .song {
    -ms-flex-flow: wrap;
        flex-flow: wrap;
  }
  .song-number {
    display: none;
  }
  .song-title {
    padding: 10px 20px;
    -ms-flex: 1 1 100%;
        flex: 1 1 100%;
    -ms-flex-order: 0;
        order: 0;
  }
  .song-view,
  .song-play {
    -ms-flex: 1 1;
        flex: 1 1;
  }
  .song-view {
    -ms-flex-pack: start;
        justify-content: flex-start;
    text-align: left;
    -ms-flex-order: 1;
        order: 1;
    padding: 0 20px 10px;
  }
  .song-view a {
    border-bottom: 2px solid #333;
  }

  .song-view a:hover,
  .song-view a:active,
  .song-view a:focus {
    border-color: #a740ff;
  }

  .song-play {
    -ms-flex-order: 2;
        order: 2;
    -ms-flex-pack: end;
        justify-content: flex-end;
    padding: 0 20px;
  }
}

.track {
  background-color: #91a3fb;
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
}

.track h2 {
  display: inline-block;
}

.track-img {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
}

.track-img img {
  width: 100%;
}

.track-details {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 20px 30px;
}

@media (min-width: 992px) {
  .track {
    -ms-flex-flow: row;
        flex-flow: row;
  }
  .track-img {
    -ms-flex: 0 1 500px;
        flex: 0 1 500px;
  }
  .track-details {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
  }
}

.error {
  color: #fff;
  background-color: red;
  width: 50%;
  padding: 5px;
  margin: 0 auto 15px auto;
}

.track-details a {
  color: #000;
}

.track-details .fa {
  font-size: 1.5em;
  margin-left: 20px;
}

.playing {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: #fff;
  text-align: left;
}

.playing > div {
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  min-width: 0;
}

.playing > div:first-child {
  -ms-flex: 0 0 85px;
      flex: 0 0 85px;
}

.track a.back {
  margin-left: 20px;
}


